.qyrr-container-wrapper {
  .components-form-token-field__input-container {
    box-sizing: border-box !important;
  }

  .editor-styles-wrapper p, .editor-styles-wrapper ol, .editor-styles-wrapper ul, .editor-styles-wrapper dl, .editor-styles-wrapper dt {
    font-family: inherit;
  }

  #qr-code-container {
    text-align: center;

    img:first-child {
      display: none !important;
    }
  }

  .wp-block {
    max-width: 100% !important;
  }

  .components-button.components-toolbar__control {
    white-space: nowrap;
    background: var(--wp-admin-theme-color);
    color: #fff;
    text-decoration: none;
    text-shadow: none;
    outline: 1px solid transparent;
  }

  .image-placeholder {
    margin-bottom: 10px;
  }

  .components-button.has-icon {
    padding: 2px;
    box-sizing: border-box;
  }

}

.logo-uploader {
  margin-bottom: 10px;
}

#font-picker {
  width: 100%;
  box-shadow: none !important;
  border: 1px solid #757575;
  margin-bottom: 10px;

  .dropdown-button {
    background: transparent !important;
  }

  .font-list {
    background: white !important;
  }
}

.color-palette-label {
  margin-bottom: calc(8px);
  display: inline-block;
}

.progress-container {
  background: #fafafa;
  box-sizing: border-box;

  div {
    padding: 20px;
    background: #5db34d !important;
    box-sizing: border-box;
  }
}

.components-notice.is-success {
  margin: 10px 0;
}